@media (max-width: 992px) {
  .buy-end__left {
    justify-content: center;
  }
}

.buy-end__right {
  display: flex;
  justify-content: center;
}

.buy-end__form {
  margin: 7rem 0 0 0;
}

.end-form {
  position: relative;
  max-width: 700px;
  margin: 4rem 45px 1rem;
  background: #ffffff;
  border-radius: 30px;
}

.end-form__left-decoration, .end-form__right-decoration {
  content: "";
  position: absolute;
  width: 50px;
  height: 20px;
  background: transparent;
  border-radius: 20px;
}

.end-form__left-decoration {
  bottom: 60px;
  left: -30px;
}

.end-form__left-decoration::before {
  top: -20px;
}

.end-form__left-decoration::after {
  top: 20px;
  left: 10px;
}

.end-form__right-decoration {
  top: 60px;
  right: -30px;
}

.end-form__right-decoration::before {
  top: -20px;
  right: 0;
}

.end-form__right-decoration::after {
  top: 20px;
  right: 10px;
}

.end-form__left-decoration::before, .end-form__left-decoration::after, .end-form__right-decoration::before, .end-form__right-decoration::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 20px;
  border-radius: 30px;
  background: white;
}

.end-form__circle {
  position: absolute;
  bottom: 80px;
  left: -55px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
}

.end-form__inner {
  padding: 50px;
}

.end-form__inner input {
  display: block;
  width: 100%;
  padding: 4px 20px;
  margin-bottom: 10px;
  background: #E9EFF6;
  line-height: 40px;
  border-width: 0;
  border-radius: 20px;
}

.end-form__inner input:focus {
  border: none;
  outline: none;
}

.end-form__inner input[type="submit"] {
  margin-top: 30px;
  background: #008edb;
  border-bottom: 4px solid #0c5278;
  color: white;
  font-size: 1.3rem;
}

.end-form__inner input[type="submit"]:hover {
  background: #109ce5;
}

@media (max-width: 380px) {
  .end-form__inner input[type="submit"] {
    font-size: 1.3rem;
    white-space: normal;
  }
}

@media (max-width: 500px) {
  .end-form__inner {
    padding: 30px;
  }
}

@media (max-width: 380px) {
  .end-form__inner {
    padding: 15px;
  }
}

.end-form__agreement {
  text-align: center;
  margin: 1.3rem 0 0 0;
}

.end-form__agreement span {
  color: red;
}

.end-form__header {
  margin: 0 0 2rem;
  font-weight: 600;
  font-size: 2rem;
  color: #454545;
  text-align: center;
}

@media (max-width: 380px) {
  .end-form__header {
    font-size: 1.6rem;
  }
}

@media (max-width: 300px) {
  .end-form__header {
    font-size: 1.5rem;
  }
}

@media (max-width: 380px) {
  .end-form {
    margin: 4rem 0px 1rem;
  }
}

.policy {
  background: #444444;
}

.policy__link {
  display: block;
  text-align: center;
  padding: 30px 10px;
  color: #fff !important;
  font-size: 1.2rem;
}

.policy__link:hover {
  color: #87c8ff !important;
}

.policy h3 {
  margin: 0;
}
